// Helper function to format currency in Vietnamese
export function formatCurrency(amount: number): string {
  return new Intl.NumberFormat("vi-VN", {
    style: "currency",
    currency: "VND",
  }).format(amount)
}

// Helper function to get status label
export function getStatusLabel(status: "pending" | "confirmed" | "shipping" | "delivered" | "cancelled"): string {
  const labels: Record<typeof status, string> = {
    pending: "Chờ xác nhận",
    confirmed: "Đã xác nhận",
    shipping: "Đang giao hàng",
    delivered: "Đã giao hàng",
    cancelled: "Đã hủy",
  }
  return labels[status]
}

// Helper function to get payment status label
export function getPaymentStatusLabel(status: "pending" | "paid" | "failed"): string {
  const labels: Record<typeof status, string> = {
    pending: "Chờ thanh toán",
    paid: "Đã thanh toán",
    failed: "Thất bại",
  }
  return labels[status]
}

// Helper to get product image URL
export function getProductImageUrl(imagePath: string): string {
  if (!imagePath) return "/placeholder.svg"
  if (imagePath.startsWith("http")) return imagePath
  if (imagePath.startsWith("/")) return `${process.env.NEXT_PUBLIC_API_BASE_URL || "http://localhost:5000"}${imagePath}`
  return `${process.env.NEXT_PUBLIC_API_BASE_URL || "http://localhost:5000"}/${imagePath}`
}
